<template>
    <div id="Login">
        <el-row :gutter="20">
            <el-col :span="7" :offset="7">
                <div class="grid-content bg-purple">
                    <h1 align="center">用户登录界面</h1>
                </div>
            </el-col>
            <el-col :span="10" :offset="6">
                <div class="grid-content bg-purple">

                    <el-form status-icon  ref="ruleForm"
                             :rules='rules'
                             :model='ruleForm'
                             class="demo-ruleForm"
                             @submit.native.prevent
                    >
                        <el-form-item label="" prop="username">
                            <el-input type="text"
                                      v-model='ruleForm.username'
                                      autocomplete="off"
                                      placeholder='请输入长度在6-20位的用户名'
                            ></el-input>
                        </el-form-item>
                        <el-form-item label="" prop="password">
                             <el-input :type="this.passwordType"
                                       v-model='ruleForm.password'
                                       autocomplete="off"
                                       placeholder='请输入长度在6-20位的密码'
                             >
                                 <i class="el-icon-view el-input__icon" :style="fontstyle" slot="suffix" @click="showPassword"></i>
                             </el-input>
                        </el-form-item>

                        <el-form-item  prop="verifycode">
                            <!-- 注意：prop与input绑定的值一定要一致，否则验证规则中的value会报undefined，因为value即为绑定的input输入值 -->
                            <el-input v-model="ruleForm.verifycode" placeholder="请输入验证码" class="identifyinput"></el-input>
                        </el-form-item>


                        <el-form-item class='identify'>
                            <div class="identifybox">
                                <div @click="refreshCode">
                                    <validate-code :identifyCode="identifyCode"></validate-code>
                                </div>
                                <el-button @click="refreshCode" type='text' class="textbtn">看不清，换一张</el-button>
                            </div>
                        </el-form-item>

                        <el-form-item>

                            <el-button @click="resetForm('ruleForm')">重置</el-button>

                            <el-button type="primary"
                                       @click="submitForm('ruleForm')"
                                       :disabled='!this.active'
                                       native-type='submit'
                            >提交</el-button>

                            <el-button type="success"
                                       @click="register"
                                       size='large'
                            >注册</el-button>
                        </el-form-item>
                    </el-form>
                </div>
            </el-col>
        </el-row>
    </div>
</template>

<script>

    import { mapMutations, mapState } from 'vuex';
    var Base64 = require('js-base64').Base64;

    export default {
        name: 'Login',
        data () {
            const validateVerifycode = (rule, value, callback) => {
                if (value === '') {
                    callback(new Error('请输入验证码'))
                    this.active = false
                } else if (value !== this.identifyCode) {
                    console.log('validateVerifycode:', value)
                    callback(new Error('验证码不正确!'))
                    this.active = false
                } else {
                    callback()
                    this.active = true
                }
            }

            let validateUserName = (rule, value, callback) => {
                if (value===''){
                    callback(new Error('用户名不能为空!'))
                    this.active = false
                } else if(value.length>20||value.length<6) {
                    callback(new Error('用户名长度必须在6~20之间'))
                    this.active = false
                }else {
                    callback()
                }
            };

            let validatePassWord = (rule, value, callback) => {
                if (value === ''){
                    callback(new Error('密码不能为空!'))
                    this.active = false
                } else if(value.length>20||value.length<6){
                    callback(new Error('密码长度必须在6~20之间'))
                    this.active = false
                } else {
                    callback()
                }
            };

            return{
                fontstyle: {
                },
                identifyCodes: '1234567890',
                identifyCode: '',
                passwordType: 'password',
                active: false,
                ruleForm: {
                    username: '',
                    password: '',
                    verifycode: '',

                },
                rules: {
                    // ruleForm: {
                        username: [
                            { validator: validateUserName, change: 'blur', required: true}
                        ],
                        password: [
                            { validator: validatePassWord, change: 'blur', required: true}
                        ],
                        verifycode: [
                            { required: true, change: 'blur', validator: validateVerifycode }
                        ]
                    // }
                }
            }

        },
        mounted() {
            // 验证码初始化
            this.identifyCode = ''
            this.makeCode(this.identifyCodes, 4)
            // this.refreshCode()
        },
        computed: {
          ...mapState(['thisBlog'])



        },
        watch: {
            'ruleForm.username':{
                handler(){
                    this.ruleForm.username = this.ruleForm.username.replace(/\s*/g,"");
                }
            },
            'ruleForm.password':{
                handler(){
                    this.ruleForm.password = this.ruleForm.password.replace(/\s*/g,"");
                }
            }
        },
        methods: {
            ...mapMutations(['getToken']),

            showPassword() {
                this.fontstyle === '' ? (this.fontstyle = 'color: red') : (this.fontstyle = '') // 改变密码可见按钮颜色
                this.passwordType === ''
                    ? (this.passwordType = 'password')
                    : (this.passwordType = '')
            },
            // 生成随机数
            randomNum(min, max) {
                return Math.floor(Math.random() * (max - min) + min)
            },
            // 切换验证码
            refreshCode() {
                this.identifyCode = ''
                this.makeCode(this.identifyCodes, 4)
            },
            // 生成四位随机验证码
            makeCode(o, l) {
                for (let i = 0; i < l; i++) {
                    this.identifyCode += this.identifyCodes[
                        this.randomNum(0, this.identifyCodes.length)
                        ]
                }
                console.log(this.identifyCode)
            },


            resetForm(ruleForm) {
                this.$refs[ruleForm].resetFields()
                // console.log(Base64.encode('980213'));
            },
            register() {
                this.$router.push('/register')
                console.log(this.ruleForm);
            },
            submitForm(ruleForm) {
                this.$axios.post('/api/user/login',{
                    "username":this.ruleForm.username,
                    "password": Base64.encode(this.ruleForm.password) //前台对密码进行加密
                }).then((res) => {
                    // response.setContentType("text/text;charset=utf-8");
                    // response.setCharacterEncoding("UTF-8");
                    console.log(res);
                    console.log(res.data.msg);
                    if (res.data.status){
                        this.$message({
                            message: res.data.msg,
                            type: 'warning'
                        });
                    } else {
                        this.$message({
                            message: res.data.msg,
                            type: 'success'
                        });
                        sessionStorage.setItem('username',this.ruleForm.username)
                        sessionStorage.setItem('grade',res.data.grade)
                        sessionStorage.setItem('user_id',res.data.user_id)
                        console.log(sessionStorage);

                        // this.$router.push('/admin')
                        if (!this.$route.query.article_id) {
                            this.$router.push('/admin')
                        } else {
                            this.$router.push({ path: '/content', query: this.$route.query.article_id})
                        }

                    }

                })
            },
        },
    }
</script>
<style scoped>
    #Login{
        height: 630px;
    }
    .identifybox{
        display: flex;
        justify-content: space-between;
        /*margin-top:7px;*/
    }
    .iconstyle{
        color:#409EFF;
    }
    .el-row{
        /*position: relative;*/
        /*margin-top: 20px;*/
    }
    h1{
        margin: 50px auto;
        font-size: 30px;
        font-weight: bold;
    }
    .el-input{
        margin:  0 auto 30px;
    }
    .el-button{
         margin: auto 30px;
     }
    .identify{
        padding: 0 80px;
    }
</style>
